.card-partner-block {
  .card-partner {
    display: flex;
    flex-direction: column;
    gap: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    padding: 30px;
    background: var(--clr-white);
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    .img {
      align-self: center;
      inline-size: 200px;
      block-size: 200px;
      img {
        inline-size: 200px;
        block-size: 200px;
      }
    }
    h4 {}
  }
}
