.label {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  color: var(--clr-white);
  font-size: 14px;
  line-height: 1.4;
  border-radius: 100vw;
}

.label.doctor {
  background-color: #6958CD;
}

.label.pharmacist {
  background-color: #1F8484;
}

.label.int-med {
  background-color: #F66B3E;
}

.label.webinar {
  background-color: #2E81CE;
}

.label.seminar {
  background-color: #F5A407;
}

.label.online {
  background-color: #14AE5C;
}

.label.recorded {
  background-color: #808080;
}
