.slider-news {
  margin-block: 30px 70px;
  .img {
    border-radius: 20px;
    img {
      border-radius: 20px;
    }
  }
  .slider-news-pagination {
    top: unset;
    bottom: -40px;
    gap: 20px;
  }
  @media (max-width: 480px) {
    .slider-news-pagination {
      gap: 10px;
    }
  }
  .splide__arrow svg {
    height: 13px !important;
    opacity: 1;
  }
  .btn-slr-page {
    opacity: 1 !important;
  }
}
