.contacts-block {
  --fz: clamp(1.00rem, calc(0.92rem + 0.41vw), 1.25rem);
  display: grid;
  grid-template-columns: 419px 1fr;
  gap: var(--m);
  margin-block-end: 20px;
  margin-block-end: clamp(0.00rem, calc(-0.41rem + 2.05vw), 1.25rem);
  .contacts {
    margin-block-start: 40px;
    h3 {
      margin-block-end: 30px;
    }
    p {
      font-size: var(--fz);
      line-height: 1.4;
    }
    .items {
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-block-end: 40px;
      &>* {
        display: flex;
        gap: 15px;
        align-items: center;
      }
      img {
        inline-size: 30px;
        block-size: 30px;
      }
    }
    address {
      font-size: var(--fz);
      line-height: 1.4;
      font-style: normal;
    }
    a {
      font-size: var(--fz);
      line-height: 1.4;
      color: var(--clr-dark);
      transition: color var(--tr);
    }
    a:hover {
      color: var(--clr-hover);
    }
    .socials {
      a {
        color: var(--clr-accent);
      }
      a:hover {
        color: var(--clr-hover);
      }
    }
  }
}

@media (max-width: 991px) {
  .contacts-block {
    grid-template-columns: 1fr;
    .contacts {
      margin-block-start: unset;
    }
  }
}

.socials {
  display: flex;
  align-items: center;
  gap: 20px;
  a {
    color: var(--clr-hover);
    transition: color var(--tr);
    svg {
      width: 30px;
      height: 30px;
      fill: currentColor;
    }
  }
  a:hover {
    color: var(--clr-hover);
  }
}
