.org-benefits {
  display: grid;
  grid-template-areas: 'header advantages' 'desc advantages';
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content 1fr;
  column-gap: var(--m);
  padding-block-start: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
  padding-block-end: clamp(3.75rem, calc(3.34rem + 2.05vw), 5.00rem);
  header {
    grid-area: header;
    max-inline-size: 528px;
    h2 {
      margin-block-end: 10px;
    }
    h4 {
      margin-block-end: 20px;
    }
  }
  .desc {
    grid-area: desc;
    max-inline-size: 528px;
    p {
      margin-block-end: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
    }
  }
  .advantages {
    grid-area: advantages;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--m);
    gap: clamp(0.94rem, calc(-0.08rem + 1.64vw), 1.25rem);
    &__item {
      padding: 30px;
      padding: clamp(0.63rem, calc(0.22rem + 2.05vw), 1.88rem);
      display: flex;
      flex-direction: column;
      gap: 9px;
      background: var(--clr-white);
      border: 1px solid var(--clr-border);
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
      border-radius: 20px;
    }
    .img {
      aspect-ratio: 1;
      max-inline-size: 90px;
      max-inline-size: clamp(3.75rem, calc(3.14rem + 3.07vw), 5.63rem);
      // max-block-size: 90px;
      // max-block-size: clamp(3.75rem, calc(3.14rem + 3.07vw), 5.63rem);
    }
  }
}

@media (max-width: 991px) {
  .org-benefits {
    grid-template-columns: 3fr 2fr;
    .desc {
      max-inline-size: 100%;
    }
    .advantages {
      justify-self: end;
      grid-template-columns: 1fr;
      gap: clamp(0.31rem, calc(0.01rem + 1.49vw), 0.94rem);
      max-inline-size: 309px;
      &__item {
        flex-direction: row;
        align-items: center;
      }
    }
  }
}

@media (max-width: 768px) {
  .org-benefits {
    display: flex;
    flex-direction: column;
    .advantages {
      display: grid;
      gap: clamp(0.31rem, calc(-0.13rem + 2.23vw), 0.94rem);
      grid-template-columns: repeat(auto-fit, minmax(236px, 1fr));
      max-inline-size: unset;
      margin-block-end: 15px;
    }
  }
}
