.card-book {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: 20px;
  .img {
    overflow: hidden;
    aspect-ratio: .8131 / 1;
    inline-size: 100%;
    border-radius: 18px;
    img {
      border-radius: 18px;
    }
  }
  .content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 20px 20px 30px;
    .author {
      margin-block-end: 5px;
      color: var(--clr-light-txt);
    }
    h4 {
      margin-block-end: 10px;
    }
    .desc {
      flex-grow: 1;
      margin-block-end: 13px;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow-y: hidden;
    }
    .price {
      margin-block-end: 10px;
    }
    .btn {
      align-self: start;
    }
  }
}

@media (max-width: 570px) {
  .card-book {
    min-inline-size: 270px;
    .img {
      aspect-ratio: .8131 / 1;
      inline-size: 100%;
    }
    .content {
      padding: 10px 15px 20px;
      h4 {
        margin-block-end: 5px;
      }
      .desc {
        margin-block-end: 5px;
      }
      .price {
        margin-block-end: 8px;
      }
      .btn {
        // align-self: start;
      }
    }
  }
}
