.container {
  --p-i: .9375rem;
  position: relative;
  inline-size: min(var(--max-container) + (var(--p-i) * 2), 100%);
  margin: 0 auto;
  padding-inline: var(--p-i);
}

.auto-grid {
  --min-w: 250px;
  --max-w: 1fr;
  --c-g: 1.25rem;
  --r-g: 1.25rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(var(--min-w), 100%), var(--max-w)));
  column-gap: var(--c-g);
  row-gap: var(--r-g);
}

h1 {
  margin-block-end: 2.5rem;
}

h2 {
  margin-block-end: 40px;
  margin-block-end: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
}

.block {
  margin-block-end: 5rem;
}

.p-0 {
  padding: 0 !important;
}

.pbs-0 {
  padding-block-start: 0 !important;
}

.m-0 {
  margin: 0 !important;
}

.mbs-2m {
  margin-block-start: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
}

.mbs-3m {
  margin-block-start: clamp(1.25rem, calc(0.43rem + 4.10vw), 3.75rem);
}

.mbs-4m {
  margin-block-start: clamp(1.25rem, calc(0.02rem + 6.15vw), 5.00rem);
}

.mbe-0m {
  margin-block-end: 0;
}

.mbe-10 {
  margin-block-end: 10px;
}

.mbe-15 {
  margin-block-end: 15px;
}

.mbe-1m {
  margin-block-end: var(--m);
}

.mbe-15m {
  margin-block-end: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
}

.mbe-2m {
  margin-block-end: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
}

.mbe-3m {
  margin-block-end: clamp(1.25rem, calc(0.43rem + 4.10vw), 3.75rem);
}

.mbe-4m {
  margin-block-end: clamp(1.25rem, calc(0.02rem + 6.15vw), 5.00rem);
}

.clr-accent {
  color: var(--clr-accent);
}

.bg-clr-white {
  background-color: var(--clr-white) !important;
}

.fw-norm {
  font-weight: var(--fw-normal);
}

.fw-mid {
  font-weight: var(--fw-medium);
}

.uc {
  text-transform: uppercase;
}

.burger {
  display: none;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px 15px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--clr-white);
  background-color: var(--clr-accent);
  border-radius: 30px;
  transition: background-color var(--tr);
}

.burger img.hidden {
  display: none;
}

.burger:hover,
.burger:focus-visible {
  background-color: var(--clr-hover);
}

@media (max-width: 1023px) {
  .burger {
    display: flex;
  }
}

.fixed {
  overflow-y: hidden;
}
