.book-slider {
  margin-block-end: 40px;
  &__slide {
    overflow: hidden;
    max-block-size: 785px;
    border-radius: 20px;
    img {
      block-size: 100%;
      object-fit: cover;
    }
  }
}

.book-slider__thumbnails {
  display: flex;
  justify-content: center;
  gap: 0 20px;
  margin-block-end: 40px;
  padding: 0;
}

.book-slider__thumbnail {
  flex-grow: 1;
  flex-shrink: 1;
  overflow: hidden;
  list-style: none;
  border-radius: 20px;
  opacity: 0.3;
  cursor: pointer;
  img {
    block-size: 100%;
  }
}

.book-slider__thumbnail.is-active {
  opacity: 1;
}
