.card-drug {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  // max-inline-size: 199px;
  padding: 30px 20px;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: var(--m);
  .img {
    max-inline-size: 121px;
  }
  .logo {
    img {
      block-size: 43px;
      object-fit: contain;
    }
  }
  .name {
    flex-grow: 1;
    text-align: center;
  }
  a {
    margin-block-end: 20px;
    color: var(--clr-accent);
  }
  a:hover,
  a:focus-visible {
    color: var(--clr-hover);
  }
}

@media (max-width: 500px) {
  .card-drug {
    gap: 5px;
    max-inline-size: 133px;
    padding: 20px 10px;
    border-radius: var(--m);
    .img {
      inline-size: 113px;
      block-size: 99px;
      img {
        height: 100%;
        object-fit: contain;
      }
    }
    .logo {
      img {
        block-size: 27px;
        object-fit: contain;
      }
    }
    .name {
      font-size: 16px;
      line-height: 20px;
    }
    a {
      margin-block-end: 10px;
    }
    .btn {
      padding: 7px 14px;
    }
  }
}
