.doc-block {
  .img {
    overflow: hidden;
    position: relative;
    border-radius: 20px;
    img {
      border-radius: 20px;
    }
    a::before {
      content: '';
      position: absolute;
      inset: 0;
      display: block;
      background: #8DC6D9;
      border-radius: 20px;
      opacity: 0;
      transition: opacity var(--tr);
    }
    a::after {
      content: url('../img/svg/icons/zoom-lb.svg');
      position: absolute;
      right: 30px;
      bottom: 30px;
      display: block;
      opacity: 0;
      transition: opacity var(--tr);
    }
    a:hover::before {
      opacity: 0.5;
    }
    a:hover::after {
      opacity: 1;
    }
  }
}

.doc-block.auto-grid {
  --min-w: 315px;
}
