.org-give-block {
  .org-give-card {
    padding: 30px;
    background: var(--clr-white);
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    .img {
      inline-size: 90px;
      block-size: 90px;
      margin-block-end: 9px;
    }
    .subtitle {
      margin-block-end: 9px;
    }
  }
}

@media (max-width: 687px) {
  .org-give-block {
    overflow-x: scroll;
    display: flex;
    gap: 5px;
    padding-block-end: 20px;
    .org-give-card {
      min-inline-size: 260px;
      padding: 15px;
    }
  }
  .org-give-block::-webkit-scrollbar {
    height: .5rem;
  }
  .org-give-block::-webkit-scrollbar-track {
    background-color: var(--clr-soft);
    border-radius: 100vw;
  }
  .org-give-block::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
}
