.accordion {
  --accor-bgc: var(--clr-light);
  --accor-fz: 22px;
  --accor-lh: 30px;
  --accor-p: 15px 20px;
  padding: var(--accor-p, 15px 20px);
  background-color: var(--accor-bgc);
  border-radius: 10px;
  &__term {
    display: flex;
    align-items: center;
    font-size: var(--accor-fz, 22px);
    line-height: var(--accor-lh, 30px);
    cursor: pointer;
    .title {
      flex-grow: 1;
    }
    svg {
      inline-size: 30px;
      block-size: 30px;
      fill: var(--clr-accent);
      transform: rotate(180deg);
      transition: transform var(--tr);
    }
    svg.rotate {
      transform: rotate(0deg);
    }
  }
  &__desc {
    max-height: 1000px;
    padding-block-start: 15px;
    font-family: var(--ff-prim);
    font-size: 16px;
    line-height: 20px;
    opacity: 1;
    transition: .3s ease-in-out;
    ul {
      counter-reset: course;
    }
    ul li::before {
      counter-increment: course;
      content: counter(course) ". ";
    }
    ul>*+* {
      margin-block-start: 10px;
    }
     :not(p:first-child) {
      margin-block-start: 10px;
    }
  }
  &__desc.hidden {
    overflow: hidden;
    max-height: 0;
    padding-block-start: 0;
    opacity: 0;
  }
}

.accordion.small {
  --accor-bgc: var(--clr-white);
  --accor-fz: 16px;
  --accor-lh: 1.4;
}

.accordion.large {
  --accor-p: 40px;
  --accor-bgc: var(--clr-white);
  --accor-fz: 28px;
  --accor-lh: 32px;
  font-family: var(--ff-sec);
  font-weight: var(--fw-m);
  border: 1px solid var(--clr-border);
  border-radius: 20px;
}

.accordion.white {
  --accor-bgc: var(--clr-white);
}
