.header-site {
  position: relative;
  z-index: 1;
  background-color: var(--clr-white);
  background-image: url('../img/misc/header-divider.png');
  background-repeat: repeat-x;
  background-position: 0 72px;
  background-position: 0 clamp(3.75rem, calc(0.93rem + 4.41vw), 4.50rem);
  .top-line {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    padding-block-start: 5px;
  }
  .logo-block {
    display: flex;
    align-items: center;
    gap: 10px;
    min-width: 231px;
    .img {
      aspect-ratio: 1;
      inline-size: 62px;
      inline-size: clamp(3.13rem, calc(0.30rem + 4.41vw), 3.88rem);
    }
    p {
      max-width: 231px;
      color: var(--clr-pressed);
      font-family: var(--ff-sec);
      font-size: 14px;
      font-size: clamp(0.75rem, calc(0.28rem + 0.74vw), 0.88rem);
      font-weight: var(--fw-m);
      line-height: 21px;
      line-height: clamp(1.13rem, calc(0.42rem + 1.10vw), 1.31rem);
      text-transform: uppercase;
    }
  }
  .contacts {
    inline-size: 100%;
    justify-content: flex-end;
    align-items: center;
    display: flex;
    gap: 20px;
    block-size: 62px;
    margin: 5px;
    margin: clamp(-0.12rem, calc(-1.77rem + 2.57vw), 0.31rem);
    li {
      a {
        position: relative;
        z-index: 1000;
        display: flex;
        align-items: center;
        gap: 5px;
        text-transform: uppercase;
        color: var(--clr-accent);
        font-size: 18px;
        font-size: clamp(0.88rem, calc(-0.07rem + 1.47vw), 1.13rem);
        line-height: 20px;
        transition: color var(--tr);
        svg {
          // aspect-ratio: 1;
          inline-size: 20px;
          block-size: 20px;
          fill: currentColor;
        }
      }
      a:hover {
        color: var(--clr-hover);
      }
    }
  }
  .menu-section {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-block-start: -67px;
    margin-block-start: clamp(-4.19rem, calc(-0.02rem + -5.15vw), -3.31rem);
  }
  .menu-list {
    display: flex;
    align-items: center;
    gap: 35px;
    gap: clamp(0.63rem, calc(-6.39rem + 10.20vw), 1.88rem);
  }
  .menu-item {
    --a-height: 74px;
    --a-height: clamp(4.00rem, calc(1.04rem + 4.63vw), 4.63rem);
    position: relative;
  }
  .menu-item a {
    display: flex;
    align-items: center;
    // gap: 5px;
    gap: clamp(0.00rem, calc(-1.48rem + 2.31vw), 0.31rem);
    block-size: var(--a-height);
    color: var(--clr-dark);
    // font-size: 18px;
    font-size: clamp(0.88rem, calc(-0.07rem + 1.47vw), 1.13rem);
    line-height: 20px;
    text-transform: uppercase;
    transition: color var(--tr);
  }
  .menu-item:hover a {
    color: var(--clr-hover);
  }
  .menu-item:hover a::after {
    transform: rotate(180deg);
  }
  .menu-item a::after {
    content: url('../img/svg/icons/arrow-dark.svg');
    inline-size: 20px;
    block-size: 20px;
  }
  .menu-item :not(a:has(+ul))::after {
    display: none;
  }
  .menu-item .menu-sub {
    position: absolute;
    top: var(--a-height);
    display: none;
    flex-direction: column;
    inline-size: max-content;
    padding-bottom: 10px;
    background-color: var(--clr-hover);
    border-radius: 0 0 10px 10px;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.05);
  }
  .menu-item .menu-sub a {
    block-size: unset;
    padding: 10px var(--m);
    color: var(--clr-white);
    font-size: 16px;
    text-transform: none;
  }
  .menu-item .menu-sub .menu-item:hover a {
    background-color: var(--clr-accent);
  }
  .menu-item:hover .menu-sub {
    display: flex;
  }
  .menu-item .menu-sub a::after {
    content: url('../img/svg/icons/arrow-light.svg');
    inline-size: 20px;
    block-size: 20px;
    transform: rotate(0deg);
  }
  .menu-item .menu-sub .menu-item:hover a::after {
    transform: rotate(-90deg);
  }
  .menu-item .menu-sub .menu-item:hover .menu-sub {
    display: flex;
  }
  .menu-item .menu-sub .menu-sub {
    overflow: hidden;
    display: none;
    top: 0;
    left: 100%;
    background-color: var(--clr-accent);
    border-radius: 0 10px 10px 10px;
  }
  .menu-item .menu-sub .menu-sub a:hover {
    background-color: var(--clr-hover);
  }
  .account {
    margin-inline-start: auto;
    // padding-block: 13px;
    // padding-block: clamp(0.63rem, calc(-0.08rem + 1.10vw), 0.81rem);
    .btn {
      gap: 10px;
      padding: 15px 25px;
      padding: clamp(0.75rem, calc(0.04rem + 1.10vw), 0.94rem) clamp(1.25rem, calc(0.07rem + 1.84vw), 1.56rem);
      font-size: 16px;
      font-size: clamp(0.88rem, calc(0.40rem + 0.74vw), 1.00rem);
      svg {
        inline-size: 20px;
        block-size: 20px;
        fill: currentColor;
      }
    }
  }
}

///////////////// M O B I L E  M E N U  ////////////////////////////////
@media (max-width: 1023px) {
  .header-site {
    position: relative;
    z-index: 200;
    // background-color: var(--clr-light);
    background-image: unset;
    .top-line {
      padding-block-start: 6px;
      padding-block-end: 6px;
      .burger {
        align-self: center;
      }
    }
    .logo-block {
      min-inline-size: 100px;
      .img {
        inline-size: 58px;
        block-size: 58px;
      }
      p {
        display: none;
      }
    }
    .menu-section {
      position: absolute;
      top: 70px;
      flex-wrap: nowrap;
      flex-direction: column;
      align-items: center;
      block-size: 100vh;
      margin-block-start: 0;
      padding-block-start: 14px;
      background-color: var(--clr-light);
      transform: translateX(-100%);
      transition: transform var(--tr);
    }
    .menu-section.active {
      transform: translateX(0%);
    }
    .contacts {
      order: 1;
      flex-direction: column;
      gap: 9px;
      inline-size: 260px;
      block-size: unset;
      margin-block-start: 0;
      padding-block: 18px;
      border-top: 1px solid var(--clr-accent);
    }
    .menu-prim {
      order: 0;
      align-self: center;
      padding-block-end: 18px;
    }
    .menu-list {
      flex-direction: column;
      gap: 9px;
      inline-size: 260px;
      color: var(--clr-accent);
    }
    .menu-item a {
      block-size: 20px;
      color: var(--clr-accent);
    }
    .menu-item a::after {
      display: none;
    }
    .menu-item:hover .menu-sub {
      display: none;
    }
    .account {
      order: 2;
      align-self: center;
      display: flex;
      flex-direction: column;
      gap: 10px;
      margin-inline-start: 0;
      padding-block: 0;
      &>.btn:nth-child(1) {
        order: 1;
      }
      .btn {
        align-self: center;
      }
    }
  }
}
