.news-section {
  display: flex;
  flex-direction: column;
  .btn {
    align-self: end;
  }
}

.news-links-block {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 20px;
  row-gap: 15px;
  margin-block-end: 30px;
  a {
    color: var(--clr-accent);
    transition: color var(--tr);
  }
  a:hover,
  a:focus-visible {
    color: var(--clr-hover);
  }
}

.card-news-block {
  margin-block-end: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
}

.card-news-block.auto-grid {
  --min-w: 315px;
  --r-g: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
}
