.card-medcenter {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: 20px;
  .img {
    overflow: hidden;
    position: relative;
    aspect-ratio: 1.5373 / 1;
    border-radius: 18px;
    img {
      border-radius: 18px;
    }
  }
  .badge {
    overflow: hidden;
    position: absolute;
    left: 20px;
    bottom: 20px;
    aspect-ratio: 1;
    inline-size: 60px;
    border: 1px solid var(--clr-border);
    border-radius: 100vw;
    img {
      border-radius: 100vw;
    }
  }
  .content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 20px 20px 25px;
    h4 {
      margin-block-end: 5px;
    }
    .category {
      color: var(--clr-light-txt);
      margin-block-end: 10px;
    }
    address {
      margin-block-end: 10px;
      font-style: normal;
    }
    .hours {
      flex-grow: 1;
      margin-block-end: 20px;
      color: var(--clr-accent);
      font-size: 14px;
    }
    .btn {
      align-self: start;
    }
  }
}

@media (max-width: 570px) {
  .card-medcenter {
    min-inline-size: 270px;
    .img {
      aspect-ratio: 1.5373 / 1;
    }
    .badge {
      left: 20px;
      bottom: 20px;
      inline-size: 60px;
    }
    .content {
      padding: 20px 20px 25px;
      h4 {
        margin-block-end: 5px;
      }
      .category {
        margin-block-end: 10px;
      }
      address {
        margin-block-end: 10px;
      }
      .hours {
        margin-block-end: 20px;
      }
      .btn {
        // align-self: start;
      }
    }
  }
}
