.course-block {
  --p1: 40px;
  --p2: 30px;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 30px;
  .desc {
    max-inline-size: 967px;
    margin-inline-start: 329px;
    h3 {
      margin-block-end: 15px;
    }
  }
  .desc>p+p {
    margin-block-start: var(--m);
  }
  .speaker {
    max-inline-size: 967px;
    margin-inline-start: 329px;
    padding: var(--p1);
    background: #FFFFFF;
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    h3 {
      margin-block-end: 10px;
    }
    .card-speaker {
      display: flex;
      align-items: center;
      gap: 20px;
      .img {
        overflow: hidden;
        min-inline-size: 88px;
        min-block-size: 88px;
        border-radius: 100vw;
        img {
          inline-size: 88px;
          block-size: 88px;
        }
      }
    }
  }
  .course-content {
    max-inline-size: 967px;
    margin-inline-start: 329px;
    padding: var(--p1);
    margin-block-end: 0;
  }
  .faq {
    max-inline-size: 967px;
    margin-inline-start: 329px;
    padding: var(--p1);
    background: var(--clr-white);
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    h3 {
      margin-block-end: 15px;
    }
  }
  .meta {
    position: absolute;
    left: 0;
    top: 0;
    inline-size: 281px;
    padding: var(--p2);
    background: var(--clr-white);
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
  }
  .meta-item {
    margin-block-end: var(--m);
    h4 {
      margin-block-end: 7px;
    }
    p {
      display: flex;
      align-items: center;
      gap: 7px;
    }
    img {
      inline-size: 24px;
      block-size: 24px;
    }
  }
  .contacts {
    position: absolute;
    left: 0;
    top: 468px;
    inline-size: 281px;
    padding: var(--p2);
    background: var(--clr-white);
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    h4 {
      margin-block-end: 20px;
    }
    a {
      display: flex;
      align-items: center;
      gap: 5px;
      margin-block-end: 10px;
      color: var(--clr-dark);
      transition: color var(--tr);
    }
    a:hover,
    a:focus-visible {
      color: var(--clr-hover);
    }
    img {
      inline-size: 24px;
      block-size: 24px;
    }
  }
  .btn-enroll {
    max-inline-size: 967px;
    margin-inline-start: 329px;
    align-self: start;
  }
}

@media (max-width: 1023px) {
  .course-block {
    gap: clamp(0.63rem, calc(0.06rem + 2.84vw), 1.88rem);
    &>* {
      margin-inline-start: unset !important;
    }
    .meta {
      order: 0;
      position: static;
      inline-size: unset;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: var(--m);
      .btn {
        align-self: center;
      }
    }
    .meta-item {
      margin-block-end: unset;
    }
    .course-content {
      order: 1;
    }
    .speaker {
      order: 2;
    }
    .desc {
      order: 3;
    }
    .faq {
      order: 4;
    }
    .contacts {
      order: 5;
      position: static;
      inline-size: max-content;
      background: var(--clr-white);
      border: 1px solid var(--clr-border);
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
      border-radius: 20px;
      h4 {
        margin-block-end: 20px;
      }
      .links {
        display: flex;
        flex-wrap: wrap;
        gap: var(--m);
      }
    }
    .btn-enroll {
      order: 6;
    }
  }
}

@media (max-width: 640px) {
  .course-block {
    --p1: 20px;
    --p2: 20px;
    .contacts {
      inline-size: unset;
      .links {
        gap: 10px;
      }
    }
  }
}

@media (max-width: 560px) {
  .course-block {
    .meta {
      gap: unset;
      row-gap: var(--m);
    }
    .meta>* {
      width: 50%;
    }
  }
}

@media (max-width: 460px) {
  .course-block {
    .speaker {
      .card-speaker {
        align-items: start;
        flex-direction: column;
        gap: 10px;
        h4 {
          margin-block-end: 8px;
        }
      }
    }
    .accordion.course-content {
      padding: var(--p1);
    }
    .accordion {
      padding: 10px;
    }
    .meta {
      justify-content: unset;
      .btn {
        width: auto;
      }
    }
    .meta>* {
      width: 100%;
    }
  }
}
