.tabs-block {
  .tablist {
    overflow: hidden;
    margin-block-end: var(--m);
    display: grid;
    grid-template-columns: repeat(3, minmax(90px, 199px));
    gap: var(--m);
    .tab {
      padding-block-end: 10px;
      color: var(--clr-border);
      background-color: transparent;
      border-bottom: 2px solid var(--clr-border);
      transition: var(--tr);
      &.selected {
        color: var(--clr-pressed);
        border-bottom-color: var(--clr-pressed);
      }
      &.selected:hover {
        color: var(--clr-pressed);
        border-bottom-color: var(--clr-pressed);
        cursor: auto;
      }
    }
    .tab:hover,
    .tab:focus-visible {
      color: var(--clr-accent);
      border-bottom-color: var(--clr-accent);
    }
  }
  .tabpanel {
    display: flex;
    flex-direction: column;
    gap: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
    .btn {
      align-self: end;
    }
  }
  .tabpanel.hidden {
    display: none;
  }
}
