.contacts-form {
  padding: 40px;
  background: var(--clr-white);
  border: 1px solid var(--clr-border);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  h3 {
    margin-block-end: 20px;
  }
  .form {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    .name {
      width: 100%;
    }
    .email {
      flex-grow: 1;
    }
    .tel {
      flex-grow: 1;
    }
    .message {
      width: 100%;
      height: 200px;
    }
    .checkbox-wrap {
      width: max-content;
      display: flex;
      align-items: center;
      gap: 20px;
      label {
        font-size: 14px;
      }
      a {
        color: var(--clr-accent);
        transition: color var(--tr);
      }
      a:hover {
        color: var(--clr-hover);
      }
    }
    .btn {
      margin-inline-start: auto;
    }
    label span {
      display: none;
    }
  }
}

@media (max-width: 991px) {
  .contacts-form {
    padding: clamp(1.25rem, calc(0.65rem + 2.98vw), 2.50rem);
    .form {
      .checkbox-wrap {
        width: unset;
      }
    }
  }
}
