.org-structure {
  display: grid;
  grid-template-areas: 'h2 staff' 'p staff' 'btn staff';
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content min-content;
  column-gap: var(--m);
  h2 {
    grid-area: h2;
    margin-block-end: var(--m);
  }
  p {
    grid-area: p;
    max-inline-size: 528px;
    margin-block-end: 30px;
  }
  .staff {
    grid-area: staff;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--m);
  }
  .staff__card {
    .img {
      inline-size: 199px;
      block-size: 199px;
      border-radius: var(--m);
      margin-block-end: 15px;
      img {
        inline-size: 199px;
        block-size: 199px;
        border-radius: var(--m);
      }
    }
    h4 {
      margin-block-end: 10px;
    }
  }
  .btn {
    grid-area: btn;
    justify-self: start;
  }
}

@media (max-width: 991px) {
  .org-structure {
    grid-template-areas: 'h2' 'p' 'staff' 'btn';
    grid-template-columns: 1fr;
    p {
      max-inline-size: unset;
      // margin-block-end: 30px;
    }
  }
}

@media (max-width: 479px) {
  .org-structure {
    .staff {
      grid-template-columns: 1fr;
      gap: unset;
    }
  }
}
