.header-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-block-end: 40px;
  margin-block-end: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
  h1 {
    margin-block-end: 0;
  }
  .search-block {
    display: flex;
    gap: 10px;
    inline-size: clamp(33.06rem, calc(-21.69rem + 88.31vw), 46.75rem);
    .search {
      flex-grow: 1;
    }
  }
}

@media (max-width: 991px) {
  .header-block {
    flex-direction: column;
    align-items: start;
    gap: var(--m);
    .search-block {
      inline-size: 100%;
      .search {
        flex-grow: 1;
      }
    }
  }
}

@media (max-width: 599px) {
  .header-block {
    .search-block {
      flex-direction: column;
      gap: 10px;
      .search {
        flex-grow: unset;
      }
      .btn {
        align-self: start;
      }
    }
  }
}
