.card-train-block {
  &.auto-grid {
    --min-w: 310px;
  }
}

@media (max-width: 687px) {
  .card-train-block {
    overflow-x: scroll;
    display: flex;
    gap: 5px;
    padding-block-end: 20px;
  }
  .card-train-block::-webkit-scrollbar {
    height: .5rem;
  }
  .card-train-block::-webkit-scrollbar-track {
    background-color: var(--clr-soft);
    border-radius: 100vw;
  }
  .card-train-block::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
}
