.slider-main {
  position: relative;
  block-size: 470px;
  color: var(--clr-white);
  a {
    position: absolute;
    inset: 0;
    display: block;
    color: var(--clr-white);
    background: linear-gradient(275.83deg, #2A7F9B 31.42%, #5DB1CD 68.27%);
    opacity: .6;
    transition: opacity var(--tr);
  }
  a:hover {
    opacity: .4;
  }
  .container {
    pointer-events: none;
  }
  h1,
  h2 {
    margin-block-start: 100px;
    margin-block-start: clamp(3.75rem, calc(2.93rem + 4.10vw), 6.25rem);
    margin-block-end: 5px;
    margin-inline-start: 110px;
  }
  h1,
  h2 {
    font-size: 52px;
    font-size: clamp(1.88rem, calc(1.42rem + 2.25vw), 3.25rem);
    line-height: 1.2;
    line-height: clamp(2.25rem, calc(1.72rem + 2.66vw), 3.88rem);
    pointer-events: none;
  }
  h4 {
    font-size: clamp(1.13rem, calc(1.04rem + 0.41vw), 1.38rem);
    line-height: clamp(1.38rem, calc(1.21rem + 0.82vw), 1.88rem);
    margin-inline-start: 110px;
    pointer-events: none;
  }
  @media (max-width: 992px) {
    h1,
    h2,
    h4 {
      margin-inline: auto;
      width: 85%;
    }
  }
  .slide {
    position: relative;
    inline-size: 1920px;
    block-size: 470px;
    background-repeat: no-repeat;
    background-position: center center;
  }
  .slide::before {
    content: '';
    position: absolute;
    inset: 0;
    display: block;
    inline-size: 100%;
    block-size: 100%;
    // background: linear-gradient(275.83deg, #2A7F9B 31.42%, #5DB1CD 68.27%);
    opacity: 0.6;
  }
  .slide--01 {
    background-image: url('../img/slider/bg-slide-01.jpg');
  }
  .slide--02 {
    background-image: url('../img/slider/bg-slide-02.jpg');
  }
  .slide--03 {
    background-image: url('../img/slider/bg-slide-03.jpg');
  }
  .splide__arrow svg {
    height: 13px !important;
    opacity: 1;
  }
  .splide__pagination {
    bottom: 15px;
  }
  .splide__pagination__page {
    margin: 10px;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-soft);
    opacity: 1;
  }
  .splide__pagination__page.is-active {
    transform: scale(1);
    background-color: var(--clr-hover);
  }
  .splide__arrows {
    position: absolute;
    z-index: 100;
    left: 50%;
    top: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between;
    inline-size: min(1326px, 100%);
    padding-inline: 15px;
    pointer-events: none;
    .splide__arrow {
      position: unset;
      opacity: 1;
      pointer-events: auto;
    }
  }
  @media (max-width: 680px) {
    .splide__arrows {
      top: 80%;
    }
  }
}

