.card-news-single {
  .img {
    overflow: hidden;
    position: relative;
    border-radius: 20px;
    margin-block-end: 20px;
    a {
      overflow: hidden;
      display: block;
      border-radius: 20px;
    }
    img {
      border-radius: 20px;
    }
  }
  .meta {
    display: flex;
    gap: 20px;
    margin-block-end: 10px;
    color: var(--clr-white);
    time {
      color: var(--clr-light-txt);
    }
    a {
      color: var(--clr-light-txt);
      transition: color var(--tr);
    }
    a:hover {
      color: var(--clr-hover);
    }
  }
  h4 {
    margin-block-end: 10px;
  }
  p {
    font-size: 14px;
    line-height: 20px;
  }
}
