@font-face {
  font-family: "Lora";
  src: url("../fonts/LoraMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Lora";
  src: url("../fonts/LoraSemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Nunito";
  src: url("../fonts/NunitoRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Nunito";
  src: url("../fonts/NunitoMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Nunito";
  src: url("../fonts/NunitoSemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Nunito";
  src: url("../fonts/NunitoBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

h1 {
  font-family: var(--ff-sec);
  font-size: 52px;
  font-size: clamp(1.88rem, calc(1.42rem + 2.25vw), 3.25rem);
  font-weight: var(--fw-sb);
  line-height: 1.2;
}

h2 {
  font-family: var(--ff-sec);
  font-size: 44px;
  font-size: clamp(1.50rem, calc(1.09rem + 2.05vw), 2.75rem);
  font-weight: var(--fw-sb);
  line-height: 1.3;
  line-height: clamp(1.88rem, calc(1.32rem + 2.77vw), 3.56rem);
}

h3 {
  font-family: var(--ff-sec);
  font-size: 28px;
  font-size: clamp(1.25rem, calc(1.09rem + 0.82vw), 1.75rem);
  font-weight: var(--fw-m);
  line-height: 1.1428;
}

h4 {
  font-size: 22px;
  font-size: clamp(1.13rem, calc(1.04rem + 0.41vw), 1.38rem);
  font-weight: var(--fw-m);
  line-height: 1.3636;
  line-height: clamp(1.38rem, calc(1.21rem + 0.82vw), 1.88rem);
}

p {
  font-size: 16px;
  line-height: 1.25;
}

.subtitle {
  font-size: 20px;
  font-size: clamp(1.13rem, calc(1.08rem + 0.20vw), 1.25rem);
  line-height: 1.4;
  line-height: clamp(1.38rem, calc(1.25rem + 0.61vw), 1.75rem);
}

.price {
  font-size: 20px;
  font-weight: var(--fw-b);
  line-height: 1.4;
}

.menu-txt {
  font-size: 18px;
  font-weight: var(--fw-sb);
  line-height: 1.1111;
  text-transform: uppercase;
}

.bold {
  line-height: 1.4;
  font-weight: var(--fw-sb);
}

.small {
  font-size: 14px;
  line-height: 1.4;
}
