.org-training {
  display: grid;
  grid-template-areas: 'h2 par' 'p par';
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content;
  column-gap: var(--m);
  margin-block-end: 40px;
  h2 {
    grid-area: h2;
    margin-block-end: 20px;
  }
  p {
    grid-area: p;
    max-inline-size: 528px;
  }
  .partners {
    grid-area: par;
    align-self: end;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--m);
  }
  .partners__card {
    // align-self: end;
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 30px;
    border: 1px solid var(--clr-border);
    border-radius: var(--m);
    .img {
      inline-size: 60px;
      block-size: 60px;
      img {
        inline-size: 60px;
        block-size: 60px;
      }
    }
    p {
      margin-block-end: 0;
    }
  }
}

@media (max-width: 991px) {
  .org-training {
    grid-template-areas: 'h2' 'p' 'par';
    grid-template-columns: 1fr;
    p {
      max-inline-size: unset;
      margin-block-end: var(--m);
    }
    .partners__card {
      flex-direction: row;
      align-items: center;
      gap: var(--m);
    }
  }
}

@media (max-width: 767px) {
  .org-training {
    .partners__card {
      flex-direction: column;
      align-items: flex-start;
      gap: 5px;
    }
  }
}

@media (max-width: 479px) {
  .org-training {
    .partners {
      grid-template-columns: 1fr;
      gap: var(--m);
    }
    .partners__card {
      padding: 15px;
    }
  }
}
