.test-modal-wrap {
  padding: 0;
  background-color: transparent;
  border: none;
}

.test-modal {
  display: flex;
  flex-direction: column;
  max-inline-size: 528px;
  padding: 30px;
  background-color: var(--clr-white);
  border: 1px solid #CEDADE;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  .top {
    display: flex;
    justify-content: space-between;
    margin-block-end: 26px;
    .logo-block {
      display: flex;
      align-items: center;
      gap: 10px;
      .img {
        inline-size: 62px;
        block-size: 62px;
      }
      p {
        font-family: 'Lora', Arial, Helvetica, sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 150%;
        text-transform: uppercase;
        color: var(--clr-pressed);
      }
    }
    .btn--close-test {
      inline-size: 40px;
      block-size: 40px;
      cursor: pointer;
    }
  }
  .mid {
    p {
      margin-block-end: 20px;
    }
  }
  .bottom {
    display: flex;
    gap: 10px;
    margin-inline-start: auto;
  }
}

.test-modal-wrap::backdrop {
  background-color: hsl(0 0% 0% / .6);
  cursor: pointer;
}

@media (max-width: 480px) {
  .test-modal {
    padding: 15px;
    .top {
      display: flex;
      justify-content: space-between;
      margin-block-end: 15px;
      .logo-block {
        p {
          display: none;
        }
      }
    }
    .mid {
      p {
        margin-block-end: 10px;
      }
    }
  }
}
