.org-terms-block {
  .org-terms-card {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 30px;
    background: var(--clr-white);
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    h3 {
      margin-block-end: 10px;
    }
    .desc {
      margin-block-end: 10px;
    }
    .price {
      margin-block-end: 30px;
    }
    .btn {
      margin-block-start: auto;
      text-align: center;
    }
  }
  .org-terms-card.info {
    padding: 30px 0 20px;
    background: unset;
    border: unset;
    box-shadow: unset;
    border-radius: unset;
    .btn {
      align-self: end;
    }
  }
}

.org-terms-block.auto-grid {
  --min-w: 315px;
}
