:root {
  --max-container: 1296px;
  --m: 1.25rem;
  --ff-prim: "Nunito", sans-serif;
  --ff-sec: "Lora", serif;
  --fz-base: 1rem;
  --fz-s: .875rem;
  --lh-base: 1.25;
  --fw-r: 400;
  --fw-m: 500;
  --fw-sb: 600;
  --fw-b: 700;
  --clr-accent: #469FBD;
  --clr-hover: #8DC6D9;
  --clr-pressed: #2A7F9B;
  --clr-light-txt: #7F8A8D;
  --clr-dark: #0D1D22;
  --clr-grey: #485D7D;
  --clr-border: #CEDADE;
  --clr-red: #DF4717;
  --clr-red-hover: #EF7048;
  --clr-light: #F0F6F8;
  --clr-soft: #BCDFEB;
  --clr-white: #fff;
  --clr-er: #f30000;
  --grad-we-green: linear-gradient(242.37deg, rgba(17, 90, 68, 0.32) 25.99%, rgba(14, 117, 105, 0.91) 74.07%);
  --grad-sn-green: linear-gradient(180deg, rgba(42, 155, 135, 0) 0%, rgba(11, 92, 73, 0.89) 85.73%);
  --grad-sn-blue: linear-gradient(180.51deg, rgba(33, 74, 137, 0.43) 17.73%, rgba(13, 69, 86, 0.82) 67.22%);
  --grad-rl-blue: linear-gradient(275.83deg, #2A7F9B 31.42%, #5DB1CD 68.27%);
  --tr: .3s ease-in-out;
}
