.card-train {
  display: flex;
  flex-direction: column;
  .card-top {
    overflow: visible;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    block-size: 199px;
    padding: 19px;
    border-top: 1px solid var(--clr-border);
    border-right: 1px solid var(--clr-border);
    border-left: 1px solid var(--clr-border);
    border-radius: 20px 20px 0 0;
    .labels {
      position: relative;
      z-index: 1;
      display: flex;
      flex-wrap: wrap;
      gap: 4px;
      pointer-events: none;
    }
    .info {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
      gap: 5px;
      margin-inline-start: 115px;
      color: var(--clr-white);
      pointer-events: none;
    }
    .badge {
      position: absolute;
      z-index: 1;
      left: 20px;
      bottom: -18px;
      max-inline-size: 98px;
      max-block-size: 98px;
      border: 1px solid var(--clr-border);
      border-radius: 100vw;
      pointer-events: none;
      img {
        border-radius: 100vw;
      }
    }
    .img {
      overflow: hidden;
      position: absolute;
      inset: 0;
      inline-size: 100%;
      block-size: 199px;
      border-radius: 19px 19px 0 0;
      img {
        border-radius: 19px 19px 0 0;
      }
      a {
        inline-size: 100%;
        block-size: 100%;
        display: block;
        img {
          block-size: 100%;
        }
      }
      a::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        display: block;
        inline-size: 100%;
        block-size: 100%;
        background-image: linear-gradient(180deg, rgba(42, 155, 135, 0) 0%, rgba(11, 92, 73, 0.89) 100%);
        opacity: 1;
        transition: opacity var(--tr);
      }
      a::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        display: block;
        inline-size: 100%;
        block-size: 100%;
        background-image: linear-gradient(180.51deg, rgba(33, 74, 137, 0.43) 17.73%, rgba(13, 69, 86, 0.82) 67.22%);
        opacity: 0;
        transition: opacity var(--tr);
      }
      a:hover::before,
      a:focus-visible::before {
        opacity: 0;
      }
      a:hover::after,
      a:focus-visible::after {
        opacity: 1;
      }
    }
  }
  .card-bottom {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px 20px;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-border);
    border-radius: 0 0 20px 20px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    .meta {
      margin-block-end: 5px;
      line-height: 22.4px;
      color: var(--clr-accent);
    }
    h3 {
      margin-block-end: 5px;
    }
    .desc {
      margin-block-end: 30px;
    }
    .price-block {
      display: grid;
      grid-template-areas: 'pr btn' 'tt btn';
      grid-template-columns: 1fr 1fr;
      gap: 5px;
      .price {
        grid-area: pr;
        align-self: end;
      }
      .train-type {
        grid-area: tt;
      }
      .btn {
        grid-area: btn;
        justify-self: end;
      }
    }
  }
}

.card-train.hidden {
  display: none;
}

@media (max-width: 687px) {
  .card-train {
    min-inline-size: 290px;
    .card-top {
      block-size: 168px;
      .info {
        gap: 3px;
        margin-inline-start: 78px;
      }
      .badge {
        left: 10px;
        bottom: 10px;
        max-inline-size: 68px;
        max-block-size: 68px;
      }
      .img {
        block-size: 168px;
      }
    }
    .card-bottom {
      padding: 20px 15px;
      .meta {
        color: var(--clr-dark);
      }
      h3 {
        color: var(--clr-accent);
        font-size: 20px;
        line-height: 26px;
      }
      .desc {
        margin-block-end: 20px;
      }
      .price-block {
        gap: unset;
        .btn {
          justify-self: start;
          align-self: center;
          padding-inline: 20px;
        }
      }
    }
  }
}
