.hero {
  position: relative;
  block-size: 470px;
  margin-block-end: 120px;
  padding-block-start: 60px;
  padding-block-start: clamp(1.88rem, calc(0.98rem + 4.46vw), 3.75rem);
  background-repeat: no-repeat;
  background-position: center center;
  &::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(275.83deg, #2A7F9B 31.42%, #5DB1CD 68.27%);
    opacity: 0.6;
  }
  .breadcrambs {
    margin-block-end: 15px;
  }
  h1 {
    margin-block-end: 20px;
    color: var(--clr-white);
  }
  h4 {
    max-inline-size: 967px;
    margin-block-end: 20px;
    color: var(--clr-white);
  }
  .benefits {
    position: absolute;
    left: 16px;
    top: 283px;
    display: flex;
    gap: 10px;
    li {
      inline-size: 270px;
      inline-size: 309px;
      padding: var(--m);
      background-color: var(--clr-white);
      border: 1px solid var(--clr-border);
      border-radius: var(--m);
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    }
    .img {
      inline-size: 60px;
      block-size: 60px;
      margin-inline-end: 10px;
    }
  }
  @media (max-width: 992px) {
    .benefits {
      position: static;
      flex-wrap: wrap;
      transform: unset;
      li {
        display: flex;
        align-items: center;
        gap: 10px;
        inline-size: 45%;
        padding: 15px;
      }
      .img {
        inline-size: 40px;
        block-size: 40px;
        margin-inline-end: unset;
        img {
          inline-size: 40px;
          block-size: 40px;
        }
      }
    }
  }
  @media (max-width: 767px) {
    .benefits {
      flex-direction: column;
      flex-wrap: unset;
      li {
        inline-size: unset;
        padding: 10px;
      }
    }
  }
  @media (max-width: 550px) {
    .benefits {
      li {
        inline-size: unset;
        padding: 10px;
      }
      .img {
        display: none;
        inline-size: unset;
        block-size: unset;
      }
    }
  }
}

@media (max-width: 992px) {
  .hero {
    margin-block-end: 80px;
    margin-block-end: clamp(2.50rem, calc(1.31rem + 5.95vw), 5.00rem);
  }
}

@media (max-width: 550px) {
  .hero {
    block-size: auto;
    padding-block-end: 30px;
    background-size: auto 100%;
  }
}

.hero--training {
  background-image: url('../img/misc/training.jpg');
}

.hero--training-doctor {
  background-image: url('../img/misc/training-doctor.jpg');
}

.hero--about {
  background-image: url('../img/misc/about.jpg');
}

.hero--course {
  margin-block-end: 80px;
  margin-block-end: clamp(1.88rem, calc(0.85rem + 5.12vw), 5.00rem);
  background: rgb(121, 175, 192);
  background: linear-gradient(90deg, rgba(121, 175, 192, 1) 0%, rgba(152, 205, 222, 1) 100%);
  &::before {
    content: unset;
  }
  .breadcrambs {
    margin-inline-start: 329px;
  }
  .hero__inner {
    margin-inline-start: 329px;
    h4 {
      max-inline-size: 638px;
    }
  }
  .course-img {
    overflow: hidden;
    position: absolute;
    z-index: 0;
    left: 16px;
    top: 0;
    inline-size: 309px;
    block-size: 185px;
    border-radius: 20px;
    img {
      block-size: 100%;
      border-radius: 20px;
    }
  }
}

@media (max-width: 992px) {
  .hero--course {
    .course-img {
      display: none;
    }
    .breadcrambs {
      margin-inline-start: unset;
    }
    .hero__inner {
      margin-inline-start: unset;
      h4 {
        max-inline-size: unset;
        margin-block-end: 40px;
      }
    }
  }
}
