.lightbox {
  position: fixed;
  overflow: hidden;
  z-index: 9999;
  width: 0;
  height: 0;
  text-align: center;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  display: grid;
  justify-content: center;
  align-items: center;
}

.lightbox img {
  max-width: 100vw;
  max-height: 100vh;
  margin-block: 0 !important;
  opacity: 0;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
  transition: opacity 500ms ease-in;
}

.lightbox:target {
  width: auto;
  height: auto;
  bottom: 0;
  right: 0;
}

.lightbox:target img {
  opacity: 1;
}
