.checkbox {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  inline-size: 19px;
  block-size: 19px;
  margin: 0;
  padding: 0;
  background: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: 3px;
  outline: none;
  appearance: none;
  cursor: pointer;
  &:checked {
    background: var(--clr-accent);
    border-color: var(--clr-accent);
  }
  &:checked:before {
    content: url('../img/svg/icons/check-white.svg');
    position: absolute;
    left: 3px;
    top: -2px;
    display: block;
    width: 12px;
    height: 9px;
  }
}

.checkbox-light {
  position: relative;
  display: grid;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 3px;
  gap: 10px;
  min-inline-size: 30px;
  min-block-size: 30px;
  background: #F6F8FB;
  border: 1px solid #E4E9F1;
  border-radius: 5px;
  outline: none;
  appearance: none;
  cursor: pointer;
  &:checked:before {
    content: url('../img/svg/icons/check-dark.svg');
    position: absolute;
    left: 9px;
    top: 4px;
    width: 12px;
    height: 9px;
  }
}
