.card-study {
  position: relative;
  h3 {
    --p: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
    position: absolute;
    z-index: 1;
    left: var(--p);
    bottom: var(--p);
    color: var(--clr-white);
    pointer-events: none;
  }
  .img {
    overflow: hidden;
    position: relative;
    aspect-ratio: 1.5 / 1;
    border-radius: var(--m);
    img {
      border-radius: var(--m);
    }
    a {
      display: block;
    }
    a::after {
      content: '';
      position: absolute;
      inset: 0;
      display: block;
      background: linear-gradient(242.37deg, rgba(17, 90, 68, 0.32) 25.99%, rgba(14, 117, 105, 0.91) 74.07%);
      opacity: 1;
      transition: opacity var(--tr);
    }
    a:hover::after,
    a:focus-visible::after {
      opacity: .5;
    }
    a.current {
      cursor: auto;
    }
    a.current::after {
      background: #049C53;
      opacity: 0.8;
    }
  }
}

.card-study--type {
  .img {
    a::after {
      background: linear-gradient(180.51deg, rgba(33, 74, 137, 0.43) 17.73%, rgba(13, 69, 86, 0.82) 67.22%);
    }
  }
}

@media (max-width: 696px) {
  .card-study {
    h3 {
      --p: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
      left: var(--p);
      bottom: var(--p);
      font-size: clamp(1.25rem, calc(0.82rem + 2.13vw), 1.75rem);
    }
  }
}
