.news-page-single-section {
  display: grid;
  grid-template-columns: 1fr 309px;
  gap: 20px;
  margin-block-end: clamp(2.50rem, calc(1.68rem + 4.10vw), 5.00rem);
  .content {
    max-inline-size: 857px;
    .links-top-line {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 20px;
      margin-block-end: 20px;
      font-size: 14px;
      a {
        color: var(--clr-accent);
        transition: color var(--tr);
      }
      a:hover {
        color: var(--clr-hover);
      }
      time {
        color: var(--clr-light-txt);
      }
      .category {
        flex-grow: 1;
      }
      .share {
        display: flex;
        align-items: center;
        gap: 7px;
      }
      img {
        inline-size: 20px;
        block-size: 20px;
      }
    }
    .img-main {
      margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
      border-radius: 10px;
      img {
        border-radius: 10px;
      }
    }
    h3 {
      margin-block-start: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
      margin-block-end: 20px;
    }
    &>p+p {
      margin-block-start: 20px;
    }
    .figure-l {
      margin-block-start: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
      margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
      .media {
        position: relative;
        margin-block-end: 20px;
        border-radius: 20px;
      }
      .img {
        display: grid;
        justify-content: center;
        align-items: center;
        border-radius: 20px;
        img {
          border-radius: 20px;
        }
      }
      .img::before {
        content: '';
        position: absolute;
        inset: 0;
        display: block;
        background-color: hsl(0 0% 0% / .7);
        border-radius: 20px;
        cursor: pointer;
        transition: background-color var(--tr);
      }
      .img:hover::before {
        background-color: hsl(0 0% 0% / .5);
      }
      .img::after {
        content: url('../img/svg/misc/play.svg');
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        cursor: pointer;
      }
      figcaption {
        font-size: 14px;
        color: var(--clr-light-txt);
      }
    }
    .figure-m {
      display: grid;
      grid-template-columns: 1fr 200px;
      gap: 20px;
      margin-block-start: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
      margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
      .media {
        position: relative;
        border-radius: 20px;
      }
      .img {
        border-radius: 20px;
        img {
          border-radius: 20px;
        }
      }
      figcaption {
        font-size: 14px;
        color: var(--clr-light-txt);
      }
    }
    .links-memo-block {
      margin-block-start: 30px;
      margin-block-end: clamp(1.88rem, calc(1.67rem + 1.02vw), 2.50rem);
      padding-block-start: 15px;
      border-block-start: 2px solid var(--clr-border);
      h4 {
        margin-block-end: 10px;
      }
    }
    .links-memo {
      display: flex;
      flex-direction: column;
      gap: 5px;
      a {
        color: var(--clr-accent);
        transition: color var(--tr);
      }
      a:hover,
      a:focus-visible {
        color: var(--clr-hover);
      }
    }
    .share-social-block {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 20px;
      margin-block-start: 30px;
      padding-block-start: 15px;
      border-block-start: 2px solid var(--clr-border);
      .social {
        display: flex;
        align-items: center;
        gap: 20px;
        a {
          color: var(--clr-accent);
          transition: color var(--tr);
          svg {
            inline-size: 30px;
            block-size: 30px;
            fill: currentColor;
          }
        }
        a:hover {
          color: var(--clr-hover);
        }
      }
    }
  }
  aside {
    padding-block-start: 40px;
    .title {
      margin-block-end: clamp(1.56rem, calc(1.26rem + 1.54vw), 2.50rem);
    }
  }
}

@media (max-width: 991px) {
  .news-page-single-section {
    grid-template-columns: 1fr;
    .content {
      max-inline-size: unset;
    }
  }
}

@media (max-width: 639px) {
  .news-page-single-section {
    .content {
      .figure-m {
        grid-template-columns: 1fr;
        gap: 20px;
        figcaption {
          font-size: 14px;
          color: var(--clr-light-txt);
        }
      }
    }
  }
}
