.filter-item {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 4px;
  inline-size: max-content;
  .category-switch {
    align-self: flex-start;
    display: inline-flex;
    align-items: center;
    gap: 5px;
    padding: 15px 20px;
    background-color: var(--clr-white);
    font-size: 16px;
    line-height: 20px;
    border: 1px solid var(--clr-border);
    border-radius: 15px;
    span {
      display: inline-block;
      line-height: 30px;
    }
    svg {
      inline-size: 30px;
      block-size: 30px;
      fill: var(--clr-accent);
      transform: rotate(0deg);
      transition: transform var(--tr);
    }
    svg.rotate {
      transform: rotate(180deg);
    }
  }
  .category-wrap {
    overflow: hidden;
    position: absolute;
    z-index: 1;
    left: 0;
    top: 66px;
    inline-size: max-content;
    inline-size: 260px;
    max-block-size: 0px;
    background-color: var(--clr-white);
    border: 1px solid transparent;
    border-radius: 15px;
    transition: var(--tr);
  }
  .category-wrap.authors {
    z-index: 2;
  }
  @media (max-width: 450px) {
    .category-wrap.themes {
      left: unset;
      right: 0;
    }
  }
  .category-inner {
    padding: 20px 15px;
  }
  .category-list {
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    gap: 10px;
    block-size: 172px;
    li {
      display: grid;
      grid-template-columns: 19px 1fr;
      gap: 5px;
      padding-inline-end: 15px;
    }
    li .checkbox {
      inline-size: 19px;
    }
  }
  .category-list::-webkit-scrollbar {
    width: 4px;
  }
  .category-list::-webkit-scrollbar-track {
    background-color: var(--clr-light);
    border-radius: 100vw;
  }
  .category-list::-webkit-scrollbar-thumb {
    background-color: var(--clr-soft);
    border-radius: 100vw;
  }
}

.category-switch.selected {
  color: var(--clr-white);
  background-color: var(--clr-accent);
  border-color: var(--clr-accent);
  svg {
    fill: currentColor;
  }
}

.category-wrap.open {
  max-block-size: 1000px;
  border: 1px solid var(--clr-border);
}
