.org-in-block {
  .org-in-card {
    display: flex;
    gap: 10px;
    padding: 20px;
    background: var(--clr-white);
    border: 1px solid var(--clr-border);
    box-shadow: 0px 0px 15px rgba(7, 92, 44, 0.05);
    border-radius: 30px;
    .desc {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
    p {
      color: var(--clr-light-txt);
    }
  }
  .org-in-card:before {
    align-self: start;
    content: '1';
    display: grid;
    justify-content: center;
    align-items: center;
    min-inline-size: 40px;
    min-block-size: 40px;
    color: var(--clr-white);
    background-color: #7B61FF;
    font-size: 22px;
    line-height: 30px;
    border-radius: 100vw;
  }
  .org-in-card:nth-child(2):before {
    content: '2';
    background-color: #5FC546;
  }
  .org-in-card:nth-child(3):before {
    content: '3';
    background-color: #F24822;
  }
}

.org-in-block.auto-grid {
  --min-w: 315px;
}
