.card-news {
  .img {
    overflow: hidden;
    position: relative;
    border-radius: 20px;
    margin-block-end: 20px;
    &>a {
      overflow: hidden;
      display: block;
      border-radius: 20px;
    }
    &>a::after {
      content: '';
      inset: 66% 0 0 0;
      position: absolute;
      display: block;
      background: linear-gradient(177.71deg, rgba(162, 169, 171, 0) 23.3%, rgba(14, 53, 66, 0.79) 68.78%);
      opacity: 0.8;
      border-radius: 0px 0px 20px 20px;
    }
    img {
      border-radius: 20px;
    }
    .meta {
      position: absolute;
      left: 20px;
      bottom: 15px;
      display: flex;
      gap: 20px;
      color: var(--clr-white);
      a {
        color: var(--clr-white);
        transition: color var(--tr);
      }
      a:hover {
        color: var(--clr-hover);
      }
    }
  }
  h3 {
    margin-block-end: 10px;
  }
  p {}
}
