.book-block {
  display: grid;
  grid-template-columns: 281px 1fr;
  align-items: start;
  grid-auto-rows: min-content;
  gap: 0 48px;
  margin-block-end: clamp(40px, 1.63rem + 4.348vi, 80px);
  &__desc {
    grid-column: 2 / -1;
    grid-row: 1 / span 1;
    margin-block-end: clamp(40px, 1.63rem + 4.348vi, 80px);
  }
  &__desc-title {
    margin-block-end: 15px;
  }
  &__desc-text>p+p {
    margin-block-start: 20px;
  }
  &__author {
    --img: 88px;
    --gap: 20px;
    grid-column: 2 / -1;
    grid-row: 2 / span 3;
    padding: 30px;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-border);
    border-radius: 30px;
  }
  &__author-title {
    margin-block-end: 15px;
  }
  &__author-details {
    display: flex;
    align-items: center;
    gap: 0 var(--gap);
    margin-block-end: 20px;
  }
  &__author-img {
    flex-shrink: 0;
    overflow: hidden;
    inline-size: var(--img);
    block-size: var(--img);
    border-radius: 100vw;
    img {
      block-size: 100%;
    }
  }
  &__author-name {
    margin-block-end: 5px;
  }
  &__author-position {
    color: var(--clr-light-txt);
  }
  &__author-text {
    margin-inline-start: calc(var(--img) + var(--gap));
  }
  &__author-text>p+p {
    margin-block-start: 20px;
  }
  &__meta {
    grid-column: 1 / span 1;
    grid-row: 1 / span 2;
    display: flex;
    flex-direction: column;
    gap: 20px 0;
    margin-block-end: 20px;
    padding: 30px;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-border);
    border-radius: 30px;
  }
  &__contacts {
    grid-column: 1 / span 1;
    grid-row: 3 / span 1;
    padding: 30px;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-border);
    border-radius: 30px;
    h4 {
      margin-block-end: 20px;
    }
  }
  &__meta-item {
    h4 {
      margin-block-end: 5px;
    }
    p {
      display: flex;
      align-items: center;
      gap: 0 10px;
    }
    img {
      inline-size: 24px;
      block-size: 24px;
    }
    span {}
  }
  &__meta-btn {
    align-self: start;
  }
  &__contacts-links {
    display: flex;
    flex-direction: column;
    gap: 10px 0;
    a {
      display: flex;
      gap: 0 10px;
      color: var(--clr-dark);
      font-size: clamp(16px, 0.913rem + 0.435vi, 20px);
    }
    img {
      inline-size: 24px;
      block-size: 24px;
    }
    span {}
  }
}

@media (max-width: 992px) {
  .book-block {
    grid-template-columns: 1fr;
    gap: 30px 0;
    &__meta {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 20px;
      margin-block-end: unset;
    }
    &__desc {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      margin-block-end: unset;
    }
    &__author {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
    &__contacts {
      grid-column: 1 / -1;
      grid-row: 4 / span 1;
      justify-self: start;
    }
    &__contacts-links {
      flex-direction: row;
      flex-wrap: wrap;
      gap: 10px;
    }
  }
}

@media (max-width: 768px) {
  .book-block {
    &__author-details {
      flex-direction: column;
      align-items: unset;
      gap: 10px 0;
    }
    &__author-text {
      margin-inline-start: unset;
    }
  }
}

@media (max-width: 428px) {
  .book-block {
    &__meta {
      flex-direction: column;
      gap: 20px 0;
    }
  }
}
