blockquote {
  margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
  padding-inline-start: 30px;
  border-inline-start: 3px solid #049C53;
  .subtitle {
    margin-block-end: 10px;
  }
  .name {
    color: var(--clr-light-txt);
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  blockquote {
    padding-inline-start: 15px;
  }
}
