.contacts-faq {
  display: grid;
  grid-template-columns: 1fr 429px;
  gap: 20px;
  margin-block-end: clamp(0.63rem, calc(0.22rem + 2.05vw), 1.88rem);
  .contacts-form {
    align-self: start;
  }
  .faq {
    h3 {
      margin-block-end: 15px;
    }
    .accordion {
      background-color: var(--clr-white);
      border: 1px solid var(--clr-border);
      dt {
        h4 {
          font-size: 16px;
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .contacts-faq {
    grid-template-columns: 1fr;
  }
}
