.footer-site {
  padding-block: 60px;
  padding-block-start: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
  padding-block-end: clamp(3.13rem, calc(2.92rem + 1.02vw), 3.75rem);
  color: var(--clr-white);
  background-color: var(--clr-accent);
  .footer-site__top {
    display: grid;
    grid-template-areas: 'b a m l i';
    grid-template-columns: 199px 199px 1fr 1fr 199px;
    gap: var(--m);
    padding-block-end: 55px;
    padding-block-end: clamp(1.88rem, calc(1.36rem + 2.56vw), 3.44rem);
  }
  .brand {
    grid-area: b;
    display: flex;
    flex-direction: column;
    .img {
      flex-grow: 1;
      inline-size: 106px;
      block-size: 110px;
    }
  }
  .association {
    grid-area: a;
  }
  .membership {
    grid-area: m;
  }
  .learning {
    grid-area: l;
  }
  .info {
    grid-area: i;
  }
  .menu-section {
    .name {
      margin-block-end: 30px;
      a {
        color: var(--clr-white);
        transition: color var(--tr);
      }
      a:hover,
      a:focus-visible {
        color: var(--clr-hover);
      }
    }
    .menu {
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
    .menu__item {
      a {
        color: var(--clr-white);
        transition: color var(--tr);
      }
      a:hover,
      a:focus-visible {
        color: var(--clr-hover);
      }
    }
  }
  &__bottom {
    display: flex;
    flex-wrap: wrap;
    padding-block-start: var(--m);
    border-top: 1px solid var(--clr-border);
    a {
      color: var(--clr-white);
      transition: color var(--tr);
    }
    a:hover,
    a:focus-visible {
      color: var(--clr-hover);
    }
    .tel,
    .email {
      display: flex;
      align-items: center;
      gap: 3px;
      svg {
        inline-size: 20px;
        block-size: 20px;
        fill: currentColor;
      }
    }
    .tel {
      flex-basis: 199px;
      margin-inline-end: var(--m);
    }
    .email {
      flex-grow: 1;
    }
    a:hover,
    a:focus-visible {
      color: var(--clr-hover);
    }
    .conf {
      margin-inline-start: 30px;
    }
    .dev {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-inline-start: 30px;
      svg {
        aspect-ratio: unset;
        inline-size: 126px;
        block-size: 15px;
        fill: currentColor;
      }
    }
  }
  @media (max-width: 1180px) {
    &__bottom {
      justify-content: space-between;
      gap: var(--m);
      .tel {
        flex-basis: unset;
        margin-inline-end: unset;
      }
      .email {
        flex-grow: unset;
      }
      .conf {
        margin-inline-start: unset;
      }
      .dev {
        margin-inline-start: unset;
      }
    }
    &__bottom>* {
      // margin-right: 100px ;
    }
  }
}

@media (max-width: 1024px) {
  .footer-site {
    .footer-site__top {
      grid-template-columns: repeat(5, 1fr);
    }
  }
}

@media (max-width: 768px) {
  .footer-site {
    .footer-site__top {
      grid-template-areas: 'b' 'a' 'm' 'l' 'i';
      grid-template-columns: 1fr;
      gap: unset;
    }
    .brand {
      flex-direction: row;
      align-items: center;
      gap: var(--m);
      margin-block-end: 20px;
      .img {
        flex-grow: unset;
        img {
          inline-size: 106px;
          block-size: 110px;
        }
      }
    }
    .menu-section {
      .name {
        margin-block-end: 10px;
      }
      .menu {
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: var(--m);
        row-gap: 10px;
        margin-block-end: 40px;
      }
    }
    .menu-section:last-child {
      .menu {
        margin-block-end: 0;
      }
    }
  }
}

@media (max-width: 390px) {
  .footer-site {
    .footer-site__top {
      gap: unset;
      .menu-section {
        .name {
          margin-block-end: 20px;
        }
        .menu {
          row-gap: var(--m);
          .menu__item {
            flex-basis: 100%;
          }
        }
      }
    }
  }
}
