.card-person {
  position: relative;
  display: flex;
  flex-direction: column;
  max-inline-size: 628px;
  padding: 30px;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: var(--m);
  .top-block {
    display: grid;
    grid-template-areas: 'i' 'p';
    margin-block-end: var(--m);
  }
  .name-block {
    h3 {
      margin-block-end: 10px;
    }
  }
  .img {
    overflow: hidden;
    inline-size: 200px;
    block-size: 200px;
    margin-block-start: -60px;
    margin-block-end: 20px;
    border: 1px solid var(--clr-border);
    border-radius: var(--m);
    img {
      inline-size: 200px;
      block-size: 200px;
      border-radius: var(--m);
    }
  }
  .position {
    color: var(--clr-light-txt);
  }
  .ranks {
    margin-block-end: var(--m);
  }
  .btn {
    margin-inline-start: auto;
  }
}

.card-person.director {
  .top-block {
    grid-template-areas: 'i p';
    gap: var(--m);
    margin-block-end: 0;
  }
}

@media (max-width: 991px) {
  .card-person.director {
    .top-block {
      grid-template-areas: 'i' 'p';
      gap: 0;
      margin-block-end: 20px;
    }
  }
}

@media (max-width: 904px) {
  .card-person.director {
    .top-block {
      grid-template-areas: 'i p';
      gap: 20px;
      margin-block-end: 0;
    }
  }
}

@media (max-width: 550px) {
  .card-person.director {
    .top-block {
      grid-template-areas: 'i' 'p';
      gap: 0;
      margin-block-end: 20px;
    }
  }
}

@media (max-width: 390px) {
  .card-person {
    padding: 20px;
    .img {
      display: none;
    }
  }
}
