.book-tabs {
  display: grid;
  gap: 20px 0;
  margin-block-end: clamp(40px, 1.63rem + 4.348vi, 80px);
  &__navlist {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0 clamp(10px, 0.408rem + 1.087vi, 20px);
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__nav-item.is-active {
    .book-tabs__nav-trigger {
      color: var(--clr-white);
      background-color: var(--clr-accent);
    }
  }
  &__nav-trigger {
    inline-size: 100%;
    block-size: clamp(40px, 1.935rem + 2.826vi, 66px);
    color: var(--clr-accent);
    background-color: transparent;
    font-weight: var(--fw-b);
    text-transform: uppercase;
    border: 2px solid var(--clr-accent);
    border-radius: 20px;
    transition: background-color var(--tr), color var(--tr);
  }
  &__panels {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    padding: 40px 0 80px;
    background-color: var(--clr-white);
    border-radius: 20px;
  }
  &__panels>* {
    grid-column: 2 / 12;
  }
  &__panel {
    display: none;
    h3 {
      margin-block-end: 15px;
    }
  }
  &__panel.is-current {
    display: block;
  }
  &__panel-body>p+p {
    margin-block-start: 20px;
  }
}

@media screen and (max-width: 600px) {
  .book-tabs {
    gap: 10px 0;
    &__navlist {
      display: grid;
      grid-template-columns: 1fr;
      gap: 10px 0;
    }
  }
}
