.card-drug-block {
  &.auto-grid {
    --min-w: 199px;
  }
}

@media (max-width: 500px) {
  .card-drug-block {
    overflow-x: scroll;
    padding-block-end: var(--m);
    &.auto-grid {
      display: flex;
      gap: 3px;
    }
  }
  .card-drug-block::-webkit-scrollbar {
    height: .5rem;
  }
  .card-drug-block::-webkit-scrollbar-track {
    background-color: var(--clr-soft);
    border-radius: 100vw;
  }
  .card-drug-block::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
}
