.breadcrambs {
  display: flex;
  flex-wrap: wrap;
  margin-block-end: 17px;
  li {
    font-size: 14px;
    line-height: 19px;
  }
  li::after {
    content: '/';
    display: inline-block;
    margin-inline: 10px;
  }
  li:last-child::after {
    content: unset;
  }
}

.breadcrambs.light {
  color: var(--clr-white);
  a {
    color: var(--clr-white);
  }
}

.breadcrambs.dark {
  margin-block-start: 60px;
  margin-block-start: clamp(1.88rem, calc(1.22rem + 3.26vw), 3.75rem);
  color: var(--clr-light-txt);
  a {
    color: var(--clr-light-txt);
  }
}

.breadcrambs {
  a {
    transition: color var(--tr);
  }
  a:hover,
  a:focus-visible {
    color: var(--clr-hover);
  }
}
