.form-modal-wrap {
  padding: 0;
  background-color: transparent;
  border: none;
}

.form-modal {
  display: grid;
  grid-template-columns: 1fr clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
  gap: clamp(0.31rem, calc(0.01rem + 1.54vw), 1.25rem);
  max-inline-size: 935px;
  background-color: transparent;
  border: none;
  .contacts-form-join {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-inline: clamp(0.63rem, calc(0.01rem + 3.07vw), 2.50rem);
    padding-block: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    background-color: var(--clr-white);
    h3,
    h4,
    p {
      text-align: center;
    }
    h3 {
      flex-grow: 1;
    }
    .small {
      max-inline-size: 696px;
    }
    .form {
      display: flex;
      flex-direction: column;
      gap: clamp(0.63rem, calc(0.42rem + 1.02vw), 1.25rem);
      .input {
        padding-block: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
        padding-inline: clamp(0.94rem, calc(0.84rem + 0.51vw), 1.25rem);
        border-color: var(--clr-light-txt);
        border-radius: unset;
      }
      .agreement {
        font-size: 14px;
        line-height: 20px;
      }
      a {
        color: var(--clr-accent);
        transition: color var(--tr);
      }
      a:hover,
      a:focus-visible {
        color: var(--clr-hover);
      }
      button[type='submit'] {
        align-self: center;
        border-radius: unset;
      }
    }
  }
  .btn--close {
    inline-size: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    block-size: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    transform: translateX(clamp(-0.62rem, calc(0.20rem + -1.02vw), 0.00rem));
    cursor: pointer;
  }
}

.form-modal-wrap::backdrop {
  background-color: hsl(0 0% 0% / .6);
  cursor: pointer;
}

