.form-modal-wrap::-webkit-scrollbar {
  width: .5rem;
}

.form-modal-wrap::-webkit-scrollbar-track {
  background-color: var(--clr-light);
  border-radius: 100vw;
}

.form-modal-wrap::-webkit-scrollbar-thumb {
  background-color: var(--clr-accent);
  border-radius: 100vw;
}
