.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 1.25rem 2.5rem;
  color: var(--clr-white);
  background-color: var(--clr-accent);
  border: none;
  border-radius: 100vw;
  font-size: var(--fz-base);
  font-weight: var(--fw-b);
  line-height: 1.25;
  text-transform: uppercase;
  border: 1px solid transparent;
  transition: all var(--tr);
}

.btn.btn--ghost {
  color: var(--clr-accent);
  background-color: transparent;
  border: 1px solid var(--clr-accent);
}

.btn:hover,
.btn:focus-visible {
  color: var(--clr-white);
  background-color: var(--clr-hover);
  border-color: transparent;
}

.btn:active {
  background-color: var(--clr-pressed);
}

.btn:disabled {
  background-color: var(--clr-soft);
  cursor: auto;
}

.btn.btn--red {
  background-color: var(--clr-red);
}

.btn.btn--red:hover,
.btn.btn--red:focus-visible {
  background-color: var(--clr-red-hover);
}

.btn.btn--red-ghost {
  color: var(--clr-red);
  background-color: transparent;
  border: 1px solid var(--clr-red);
}

.btn.btn--red-ghost:hover,
.btn.btn--red-ghost:focus-visible {
  color: var(--clr-white);
  background-color: var(--clr-red);
  border-color: transparent;
}

@media (max-width: 768px) {
  .btn {
    padding: .9375rem 1.875rem;
    font-size: var(--fz-s);
    line-height: 1.4285;
  }
}


/* ----- slider btns --------------*/

.btn-slr {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  inline-size: 2.5rem;
  block-size: 2.5rem;
  color: var(--clr-accent);
  background-color: var(--clr-light);
  border: none;
  border-radius: .9375rem;
  opacity: 1 !important;
  svg {
    inline-size: 1.875rem;
    block-size: 1.875rem;
    fill: currentColor;
  }
}

.btn-slr.next {
  svg {
    transform: rotate(0deg);
  }
}

.btn-slr.prev {
  svg {
    transform: rotate(180deg);
  }
}

.btn-slr:active {
  color: var(--clr-white);
  background-color: var(--clr-pressed);
}


/* ----- slider pages --------------*/

.btn-slr-page {
  display: inline-block;
  inline-size: 1.25rem;
  block-size: 1.25rem;
  background-color: var(--clr-light);
  border-radius: 100vw;
}

.btn-slr-page.is-active {
  background-color: var(--clr-accent);
}

.btn-slr-page-news {
  display: inline-block;
  inline-size: 20px;
  block-size: 20px;
  background-color: var(--clr-light);
  border-radius: 100vw;
  border: 1px solid var(--clr-accent);
  opacity: 1 !important;
}

.btn-slr-page-news.is-active {
  background-color: var(--clr-accent);
  transform: scale(1);
  opacity: 1 !important;
}
