@media (max-width: 570px) {
  .card-medcenter-block {
    overflow-x: scroll;
    padding-block-end: var(--m);
  }
  .card-medcenter-block.auto-grid {
    display: flex;
    gap: 3px;
  }
  .card-medcenter-block::-webkit-scrollbar {
    height: .5rem;
  }
  .card-medcenter-block::-webkit-scrollbar-track {
    background-color: var(--clr-soft);
    border-radius: 100vw;
  }
  .card-medcenter-block::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent);
    border-radius: 100vw;
  }
}
